<template>
    <p-data-table
        :value="cycleInfo"
        responsiveLayout="scroll"
        class="p-datatable-sm"
        :scrollable="true"
        scrollHeight="100px"
    >
        <p-column header="" field="label" style="width: 120px" />
        <p-column header="" field="description" />
    </p-data-table>
</template>

<script>
import { computed, inject, reactive } from "vue";
export default {
    setup() {
        const selectedBloodTest = inject("selectedBloodTest");
        const cycleInfo = reactive([
            {
                label: "Cycle No",
                description: computed(() => selectedBloodTest.value.cycleId)
            },
            {
                label: "Patient",
                description: computed(() => selectedBloodTest.value.patientId)
            },
            {
                label: "Blood Test Id",
                description: computed(() => selectedBloodTest.value.testOrderId)
            },
            {
                label: "Order Date",
                description: computed(
                    () => selectedBloodTest.value.bloodTestOrderDate
                )
            },
            {
                label: "Result Date",
                description: computed(
                    () => selectedBloodTest.value.bloodTestResultDate
                )
            }
        ]);

        return {
            cycleInfo
        };
    }
};
</script>

<style scoped>
:deep() .p-datatable-thead {
    display: none;
}
</style>
