<template>
    <p-data-table
        :value="testResults"
        responsiveLayout="scroll"
        showGridlines
        class="p-datatable-sm"
        :loading="loading"
        v-model:selection="selectedBloodTest"
        selectionMode="single"
        dataKey="testOrderId"
        :scrollable="true"
        scrollHeight="calc(100vh - 130px)"
    >
        <p-column header="ID" field="testOrderId" />
        <p-column header="Order Date" field="bloodTestOrderDate" />
        <p-column header="E2" field="etwo">
            <template #body="{data}">
                {{
                    data.etwo === null || data.etwo === ""
                        ? data.etwoStat
                        : data.etwo
                }}
            </template>
        </p-column>
        <p-column header="LH" field="lh">
            <template #body="{data}">
                {{ data.lh === null || data.lh === "" ? data.lhStat : data.lh }}
            </template>
        </p-column>
        <p-column header="PROG" field="prog">
            <template #body="{data}">
                {{
                    data.prog === null || data.prog === ""
                        ? data.progStat
                        : data.prog
                }}
            </template>
        </p-column>
        <p-column header="FSH" field="fsh">
            <template #body="{data}">
                {{
                    data.fsh === null || data.fsh === ""
                        ? data.fshStat
                        : data.fsh
                }}
            </template>
        </p-column>
        <p-column header="TSH" field="tsh">
            <template #body="{data}">
                {{
                    data.tsh === null || data.tshStat == ""
                        ? data.tshStat
                        : data.tsh
                }}
            </template>
        </p-column>
        <p-column header="PRL" field="prl">
            <template #body="{data}">
                {{
                    data.prl === null || data.prl === ""
                        ? data.prlStat
                        : data.prl
                }}
            </template>
        </p-column>
        <p-column header="BhCG" field="bhcg">
            <template #body="{data}">
                {{
                    data.bhcg === null || data.bhcg === ""
                        ? data.bhcgStat
                        : data.bhcg
                }}
            </template>
        </p-column>
        <p-column header="DHEAS" field="dheas">
            <template #body="{data}">
                {{
                    data.dheas === null || data.dheas === ""
                        ? data.dheasStat
                        : data.dheas
                }}
            </template>
        </p-column>
        <p-column header="AMHP" field="amh">
            <template #body="{data}">
                {{
                    data.amh === null || data.amh === ""
                        ? data.amhStat
                        : data.amh
                }}
            </template>
        </p-column>
        <p-column header="" sty>
            <template #body="{data}">
                <span class="p-buttonset">
                    <p-button
                        label="Barcode"
                        class="p-button-sm"
                        @click="showBarcodeV2(data)"
                    />
                    <p-button
                        class="p-button-sm"
                        icon="pi pi-print"
                        @click="printToZebraPrinter(data)"
                    />
                </span>
            </template>
        </p-column>

        <template #footer>
            <p-button
                label="Order Test"
                class="p-mr-2"
                @click="add"
                v-if="hasCreatePermission"
            />
            <p-button
                label="Edit"
                class="p-mr-2"
                @click="edit"
                v-if="hasUpdatePermission"
            />
            <p-button label="Report" class="p-mr-2" @click="openPdfReport" />
        </template>
    </p-data-table>
    <p-dialog v-model:visible="anotherPatientDialogue" modal header="Different patient" :style="{ width: '30rem' }" >
        <p>
           Go to Patient {{ anotherPatientData.patientId }}
        </p>
        <p-button label="No" class="p-mr-2" @click="cancel"/>
        <p-button label="Yes" class="p-button-danger"  @click="goForPatient" />
    </p-dialog>
</template>

<script>
import { useBloodTestsReportInit } from "@/composable/blood-tests";
import { inject, onMounted, onUnmounted, ref } from "vue";
import { useToast } from "primevue/usetoast";
import { emitter } from "@/event-bus";
import { usePermissionCheck } from "@/composable/users";
import { ZEBRA_PRING_URL } from '@/constants';
import beepSound from '@/assets/pos.mp3';
import buzzSound from '@/assets/neg.mp3';
import router from "@/router";
import { $axios } from "@/service/app-http";

export default {
    setup() {
        const {
            store,
            testResults,
            loading,
            getBloodTestResults,
            getBloodTestResultsWithOpenBarcode,
            orderBloodTestConfirmDialog,
            editDailogVisible,
            selectedBloodTest,
            showBarcode,
            showBarcodeV2,
            openPdfReport
        } = useBloodTestsReportInit();

        //const router = useRouter();

        const anotherPatientDialogue = ref(false);

        const cancel = () => {
            anotherPatientDialogue.value = false;
        }

        const playBeep = (_flag) => {
            if(_flag == 1) {
                const audio = new Audio(beepSound); 
                audio.play();
            } else {
                const audio = new Audio(buzzSound); 
                audio.play();
            }  
        };
        
        const anotherPatientData = ref(null);
        const goForPatient = () => {
                        router.push({
                                name: "redirect",
                                query: {
                                    patientId: anotherPatientData.value.patientId,
                                    to: anotherPatientData.value.scanarea
                                }
                            });  
        }

        const checkScannedBarcode = async (_barcode)  => {            
            if(testResults.value) {

                // Check if _barcode matches any test_id in bloodTestResults
                const isBarcodeMatch = testResults.value.some(row => row.testOrderId === _barcode);

                // Output the result
                if (isBarcodeMatch) {
                    playBeep(1);
                    // Example: Use toast to display the value
                    toast.add({
                        severity: "success",
                        summary: _barcode,
                        detail: "Barcode is correct. Confirmed!",
                        life: 3000
                    });
                } 
                else {
                   
                    if(store.state.artCycle.patient && store.state.artCycle.patient.chart == _barcode) {

                        playBeep(1);
                        toast.add({
                            severity: "warn",
                            summary: _barcode,
                            detail: "Patient is correct. Confirmed!",
                            life: 3000
                         });

                        return;
                    } 


                    playBeep(0);
                    toast.add({
                        severity: "error",
                        summary: _barcode,
                        detail: "Barcode is not correct. Wrong!",
                        life: 3000
                    });
                    
                    const scanarea = 'blood-tests';
                    
                        try {
                            const { data } = await $axios.get(`scan-barcode/data?barcode=${_barcode}&scanarea=${scanarea}`);
                            
                            anotherPatientData.value = data;
                            anotherPatientDialogue.value = true;

                        } catch (e) {
                            console.log(e);
                        }

                    

                    // const anotherPatient = '105337';
                    // const pageName = 'blood-tests';         

                }
            }
                // Example: Log the value
                // console.log("Scanned barcode:", _barcode);


               
            
        };

        const toast = useToast();

        const {
            hasCreatePermission,
            hasUpdatePermission
        } = usePermissionCheck();

        const printToZebraPrinter = (data) => {
            let patient = { ...store.state.artCycle.patient };

            let base = `${ZEBRA_PRING_URL}/bt_zpl_print.php`;

            let url =
                `${base}` +
                `?first_name=${patient.firstName}` +
                `&last_name=${patient.lastName}` +
                `&chart=${patient.chart}` +
                `&dob=${patient.year}-${patient.month}-${patient.day}` +
                `&gender=${patient.sex}` +
                `&test_order_id=${data.testOrderId}` +
                `&test_orders=${data.testItems}`;

            window.open(url);
        };


        onMounted(() => {
            getBloodTestResults();

            emitter.on(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );

            emitter.on(
                "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                getBloodTestResultsWithOpenBarcode
            );

            emitter.on("cycles-ui:check-barcode", checkScannedBarcode);
        });

        onUnmounted(() => {
            emitter.off(
                "blood-test-edit-dailog:reload-test-results",
                getBloodTestResults
            );

            emitter.off(
                "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                getBloodTestResultsWithOpenBarcode
            );

            emitter.off("cycles-ui:check-barcode", checkScannedBarcode);
        });

        return {
            anotherPatientData,
            anotherPatientDialogue,
            cancel,
            goForPatient,
            testResults,
            selectedBloodTest,
            loading,
            showBarcode,
            showBarcodeV2,
            openPdfReport,
            add: () => {
                orderBloodTestConfirmDialog.value = true;
            },
            edit: () => {
                if (selectedBloodTest.value == null) {
                    toast.add({
                        severity: "warn",
                        summary: "Select Row",
                        detail: "Please select a row first",
                        life: 3000
                    });
                    return;
                }
                editDailogVisible.value = true;
            },
            hasCreatePermission,
            hasUpdatePermission,
            printToZebraPrinter
        };
    }
};
</script>
